(function() {
  angular.module('App').controller('NumbersController', function($rootScope, $scope, $state, $window, $timeout, $interval, SocketService, SweetAlert) {
    'ngInject';
    var activeLevel;
    $scope.status = {};
    activeLevel = 'numbers';
    $scope.answerCode = 'iloveyou';
    $scope.correctTiles = false;
    $scope.userInputCode = '';
    $scope.$watch('userInputCode', function() {
      if (!$scope.correctTiles) {
        $scope.codeStatus = '';
        return;
      }
      if ($scope.userInputCode.length === 0) {
        return $scope.codeStatus = '';
      } else if (angular.equals($scope.userInputCode.toLowerCase().replace(/ /g, ''), $scope.answerCode)) {
        return $scope.codeStatus = 'success';
      } else {
        return $scope.codeStatus = 'error';
      }
    });
    $scope.start = function() {
      var payload;
      payload = {
        kind: 'level',
        value: activeLevel
      };
      return SocketService.masterTransmit(payload);
    };
    $scope.start();
    $scope.answerTotal = {
      sum: 4181,
      count: 8
    };
    $rootScope.$on('slaveIncoming', function(event, data) {
      if (data.kind === 'level') {
        $state.go('slave.' + data.value);
        return;
      }
      if (data.level !== activeLevel) {
        return;
      }
      switch (data.kind) {
        case 'tileStatus':
          $scope.tiles = data.tiles;
          return $scope.$apply();
      }
    });
    $scope.$watch('tiles', function() {
      $scope.tilesCount = 0;
      $scope.tilesSum = 0;
      angular.forEach($scope.tiles, function(tile) {
        if (tile.selected) {
          $scope.tilesCount += 1;
          return $scope.tilesSum += tile.value;
        }
      });
      if ($scope.tilesSum === $scope.answerTotal.sum && $scope.tilesCount === $scope.answerTotal.count) {
        return $scope.correctTiles = true;
      } else {
        return $scope.correctTiles = false;
      }
    });
  });

}).call(this);



