(function() {
  angular.module('App').controller('CompassSlaveController', function($rootScope, $scope, $state, $window, $timeout, $interval, SocketService, SweetAlert) {
    'ngInject';
    var activeLevel, paintBackground;
    $scope.status = {};
    activeLevel = 'compass';
    $scope.arrowStyle = function() {
      return {
        'transform': 'rotate(' + $scope.displayOrientation + 'deg)'
      };
    };
    $scope.$watch('orientation', function(newValue, oldValue) {
      if (Math.abs(newValue - oldValue) > 1 || (oldValue == null)) {
        return $scope.displayOrientation = newValue;
      }
    });
    $rootScope.$on('masterIncoming', function(event, data) {
      if (data.kind === 'level') {
        $state.go('slave.' + data.value);
        return;
      }
      if (data.level !== activeLevel) {
        return;
      }
      switch (data.kind) {
        case 'status':
          return paintBackground(data.value);
      }
    });
    $scope.compassClick = function() {
      return SocketService.slaveTransmit({
        level: activeLevel,
        kind: 'arrowClick',
        angle: $scope.displayOrientation
      });
    };
    paintBackground = function(status) {
      var timeout;
      $scope.status.color = status;
      if (typeof timeout !== "undefined" && timeout !== null) {
        $timeout.cancel(timeout);
      }
      return timeout = $timeout(function() {
        return $scope.status.color = void 0;
      }, 1000);
    };
    $window.ondeviceorientation = function(event) {
      $scope.orientation = event.alpha;
      return $scope.$apply();
    };
  });

}).call(this);



(function() {
  angular.module('App').controller('CompassController', function($rootScope, $scope, $state, $window, $timeout, $interval, SocketService, SweetAlert) {
    'ngInject';
    var activeLevel, angleOffset, checkAngle, currentAngleStep;
    $scope.status = {};
    activeLevel = 'compass';
    $scope.code = '';
    $scope.start = function() {
      var payload;
      payload = {
        level: activeLevel,
        kind: 'level',
        value: activeLevel
      };
      return SocketService.masterTransmit(payload);
    };
    $scope.start();
    $scope.angles = [45, 225, 135, 315];
    $scope.correctAngles = 0;
    currentAngleStep = 0;
    angleOffset = 22.5;
    $rootScope.$on('slaveIncoming', function(event, data) {
      if (data.kind === 'level') {
        $state.go('slave.' + data.value);
        return;
      }
      if (data.level !== activeLevel) {
        return;
      }
      switch (data.kind) {
        case 'arrowClick':
          return checkAngle(data.angle);
      }
    });
    $scope.$watch('code', function(newValue, oldValue) {
      var solution, status;
      if (oldValue.length >= newValue.length || newValue.length === 0) {
        return;
      }
      solution = 'nsew';
      status = 'error';
      if (solution.indexOf($scope.code.toLowerCase()) === 0) {
        status = 'success';
        if ($scope.code.toLowerCase() === solution) {
          alert("succes");
        }
      }
      return SocketService.masterTransmit({
        level: activeLevel,
        kind: 'status',
        value: status
      });
    });
    $interval(function() {
      currentAngleStep++;
      if (currentAngleStep >= $scope.angles.length) {
        return currentAngleStep = 0;
      }
    }, 2000);
    $scope.arrowStyle = function() {
      return {
        'transform': 'rotate(' + $scope.angles[currentAngleStep] + 'deg)'
      };
    };
    checkAngle = function(angle) {
      var status;
      status = 'error';
      console.log($scope.angles[$scope.correctAngles] - angleOffset, $scope.angles[$scope.correctAngles] + angleOffset);
      if (angle > $scope.angles[$scope.correctAngles] - angleOffset && angle < $scope.angles[$scope.correctAngles] + angleOffset) {
        status = 'success';
        $scope.correctAngles++;
        if ($scope.correctAngles > $scope.angles.length - 1) {
          console.log('level done');
        }
      } else {
        status = 'error';
        $scope.correctAngles = 0;
      }
      return SocketService.masterTransmit({
        level: activeLevel,
        kind: 'status',
        value: status
      });
    };
  });

}).call(this);



(function() {
  angular.module('App').service('SocketService', function($rootScope, socketUrl) {
    'ngInject';
    return {
      socket: io.connect(socketUrl),
      initiate: function() {
        this.socket.on('slaveIncoming', function(data) {
          console.log(data);
          return $rootScope.$broadcast('slaveIncoming', data);
        });
        this.socket.on('masterIncoming', function(data) {
          return $rootScope.$broadcast('masterIncoming', data);
        });
        this.unsubscribe('abc');
        return this.subscribe('abc');
      },
      subscribe: function(room) {
        this.room = room;
        return this.socket.emit('subscribe', room);
      },
      unsubscribe: function(room) {
        this.room = room;
        return this.socket.emit('unsubscribe', room);
      },
      slaveTransmit: function(data) {
        data.room = this.room;
        return this.socket.emit('slaveTransmit', data);
      },
      masterTransmit: function(data) {
        data.room = this.room;
        return this.socket.emit('masterTransmit', data);
      }
    };
  });

}).call(this);



(function() {
  angular.module('App').run(function($log, $rootScope, $state, $timeout, SocketService) {
    'ngInject';
    $log.debug('runBlock end');
    SocketService.initiate();
    $rootScope.levels = [
      {
        key: 'haiku',
        name: 'Haiku'
      }, {
        key: 'compass',
        name: 'Compass'
      }, {
        key: 'winner',
        name: 'Winner'
      }
    ];
    $rootScope.currentLevel = 0;
    return $rootScope.nextLevel = function() {
      $rootScope.currentLevel += 1;
      return $state.go('app.' + $rootScope.levels[$rootScope.currentLevel].key);
    };
  });

}).call(this);



